
import $ from 'jquery'
import jsonData from "../json/json.json"
export default {
    /**getting variables for select boxes**/
    getCountries: function (jsonData) {
        var uniqueCountry = [];
        var countries = [];
        $(jsonData).each(function () {
            countries.push(this.winner_region)
        });
        uniqueCountry = countries.filter(function (item, pos) {
            return countries.indexOf(item) == pos;
        })
        return uniqueCountry;
    },
    getCategories: function (jsonData) {
        var uniqueCategories = [];
        var categories = [];
        $(jsonData).each(function () {
            categories.push(this.award_cat_title)
        });
        uniqueCategories = categories.filter(function (item, pos) {
            return categories.indexOf(item) == pos;
        });
        // console.log()
        return uniqueCategories;
    },
    getQuarters: function (jsonData) {
        var uniqueQuarter = [];
        var quarter = [];
        $(jsonData).each(function () {
            quarter.push(this.running_q)
        });
        uniqueQuarter = quarter.filter(function (item, pos) {
            return quarter.indexOf(item) == pos;
        });
        return uniqueQuarter;
    },
    /*****Unused function */
    pageCounter: function () {
        var myStorage = window.localStorage, pageCount;
        window.addEventListener('load', function () {
            if (!myStorage.getItem("pageCount")) {
                myStorage.setItem('pageCount', 1);
            } else {
                pageCount = myStorage.getItem("pageCount");
                pageCount = pageCount + 1;
                myStorage.setItem('pageCount', pageCount);
            }
            console.log('page view count', myStorage.getItem("pageCount"));
        });
    },
    /***
     * Reformatting json data to re-inject fullname 
     */
    formattedJson: function () {
        var items = jsonData.item;
        var arr = [];
        $(items).each(function () {
            this["winner_full_name"] = this.winner_first_name + " " + this.winner_last_name;
            arr.push(this);
        });
        return arr;
    },
    /**Unused function */
    formattedPage: function () {
        var items = jsonData.item;
        var arr = [];
        var counter = 0;
        $(items).each(function () {
            counter++;
            if(counter%12){
                arr.push(this);
            }
        });

        return arr;
    }
}
