<script>
import $ from 'jquery'
import jsonData from "../assets/json/json.json"
import App from "../assets/js/app.js"
export default {
  name: 'FilteredView',
  props: {
    msg: String
  },
  methods: {
    filter: App.filter,
    filterBy() {

      // console.log(this.selCategory);
      // console.log(this.selCountry);

      var quarterArray = [];
      var categArray = [];
      var countryArray = [];
      var searchArray = [];
      var running_q = [];
      var award_cat_title = [];
      var winner_country = [];
      // var winner_first_name = [];
      // var winner_last_name = [];
      var filterBy = [];



      if (this.selQuarter != '') {
        quarterArray.push(this.selQuarter);
        running_q = quarterArray;
        filterBy['running_q'] = running_q;
      }

      if (this.selCategory != '') {
        categArray.push(this.selCategory);
        award_cat_title = categArray;
        filterBy['award_cat_title'] = award_cat_title;
      }

      if (this.selCountry != '') {
        countryArray.push(this.selCountry);
        winner_country = countryArray;
        filterBy['winner_country'] = winner_country;
      }

      if (this.txtSearch != '') {
        searchArray.push(this.txtSearch);
        // winner_first_name = searchArray;
        // winner_last_name = searchArray;
        // console.log(winner_first_name);
        // filterBy['winner_first_name'] = winner_first_name;
        // filterBy['winner_last_name'] = " Hunt";
      }


      // console.log(award_cat_title);
      // console.log(winner_country);

      // var data = [{ VOTER: 1012, PARTY: "REPUBLICAN", PRECINCT: 2408, AGE_GROUP: "71 +", LAST_VOTED: "08/2006", YEARS_REG: 51, BALLOT_STATUS: "PERM" }, { VOTER: 1013, PARTY: "REPUBLICAN", PRECINCT: 2411, AGE_GROUP: "71 +", LAST_VOTED: "08/2006", YEARS_REG: 50, BALLOT_STATUS: "PERM" }, { VOTER: 1014, PARTY: "DEMOCRAT", PRECINCT: 2424, AGE_GROUP: "71 +", LAST_VOTED: "08/2006", YEARS_REG: 50, BALLOT_STATUS: "PERM" }, { VOTER: 1015, PARTY: "DEMOCRAT", PRECINCT: 2418, AGE_GROUP: "71 +", LAST_VOTED: "08/2006", YEARS_REG: 50, BALLOT_STATUS: "POLL" }, { VOTER: 1109, PARTY: "AMERICAN INDEP", PRECINCT: 2404, AGE_GROUP: "71 +", LAST_VOTED: "08/2006", YEARS_REG: 34, BALLOT_STATUS: "POLL" }, { VOTER: 1111, PARTY: "DECLINED", PRECINCT: 2414, AGE_GROUP: "71 +", LAST_VOTED: "08/2006", YEARS_REG: 34, BALLOT_STATUS: "POLL" }],
      var data = JSON.stringify(jsonData.item);
      data = JSON.parse(data);
      // filterBy = { award_cat_title: ["Business Growth"], BALLOT_STATUS: ["PERM", "POLL"] }
      // var filterBy = { award_cat_title: categArray, winner_country: regionArray };
      var result = data.filter(function (o) {
        return Object.keys(filterBy).every(function (k) {
          return filterBy[k].some(function (f) {
            return o[k] === f;
          });
        });
      });

      if (this.txtSearch != "") {
        result = JSON.stringify(result);
        result = JSON.parse(result);
        var search = this.txtSearch;


        if (!search.includes("@")) {
          console.log("Searching full name...");
          result = JSON.stringify(result);
          result = JSON.parse(result);
          result = result.filter(i => i.winner_full_name.toLowerCase().includes(search.toLowerCase()));
        } else {
          console.log("Searching email address...");
          result = JSON.stringify(result);
          result = JSON.parse(result);
          result = result.filter(i => i.winner_email.toLowerCase().includes(search.toLowerCase()));
        }
      }

      var arrPages = [];
      var minCounter = 1;
      var i = 1;

      //page counter
      var maxCounter = (this.currentPage * 12);
      minCounter = ((maxCounter + 1) - 12);
      var maxPage = Math.ceil(result.length / 12);


      console.log("MINcOUNTER" + minCounter);
      console.log(maxCounter);

      for (i = 1; i <= maxPage; i++) {
        arrPages.push(i);
      }
      console.log(arrPages);
      this.myPages = arrPages;

      var counter = 0;
      var newItems = [];
      $(result).each(function () {
        if (counter >= minCounter && counter <= maxCounter) {
          newItems.push(this);
        }
        counter++;
      });
      console.log(this);
      this.items = newItems;
      return this.items;
