<template>


  <!-- Required meta tags -->
  <meta charset="utf-8">
  <meta name="viewport" content="width=device-width, initial-scale=1, shrink-to-fit=no">

  <div class="container">
    <div class="mt-2">&nbsp;</div>
    <div id="header" class="mt-5 row">
      <div class="col-5">
        <div id="page-title" class="header">
          <h1>Oracle Test</h1>
        </div>
        <hr />
        <p>The task is to display the data below that includes the filter feature working and user search
          function.</p>
      </div>
      <div class="col-5"></div>
      <div class="col-2">
        <div id="page-visits" class="card">
          <div class="text-center card-body">
            <h2>{{ this.pageCount }}</h2>
            <span>Number of time users visit this page</span>
          </div>
        </div>
      </div>
    </div>
    <div id="filter" class="mt-3 row">
      <div class="col">
        <div class="card">
          <div id="filter-header" class="card-header">
            <div class="row">
              <div class="col-2">
                <select v-model="selQuarter" id="quarter" title="quarter" name="quarter"
                  @change="this.filterByParameters">
                  <option value="">All Quarters</option>
                  <option v-for="quarter in quarters" :key="quarter" :value="quarter">{{ quarter }}</option>
                </select>
              </div>
              <div class="col-3"></div>
              <div class="col-7">
                <div class="row">
                  <div class="col">
                    <div class=" text-right">
                      <label id="filter-by-text" class="mt-2 align-middle ">Filter By :</label>
                      <select id="categories" v-model="selCategory" @change="this.filterByParameters">
                        <option value="">All Categories</option>
                        <option v-for="categ in categories" :key="categ" :value="categ">{{ categ }}</option>
                      </select>
                      <select id="countries" v-model="selCountry" @change="this.filterByParameters">
                        <option value="">All Regions</option>
                        <option v-for="country in countries" :key="country" :value="country">{{ country }}</option>
                      </select>
                      <input type="text" v-model="txtSearch" v-on:keyup="this.filterByParameters" class="d-inline"
                        id="search" placeholder="Search by name...">
                    </div>
                  </div>
                </div>

              </div>
            </div>
          </div>
          <div id="filter-card-container" class="card-body">

            <div class="row">
              <div class=" col-4" v-for="item in this.items" :key="item.id">
                <div class="card mb-4">
                  <div class="card-body">
                    <div><strong class="item-cat-title">{{ item.award_cat_title }}</strong></div>
                    <i>for</i> <span class="item-cat-sub-title">{{ item.award_cat_sub_title }}</span>
                    <div class="mt-3">
                      <small><strong class="item-winner-country">{{ item.winner_region }}</strong></small>
                      <div><span class="item-name">{{ item.winner_first_name }} {{ item.winner_last_name }}</span></div>
                      <div>
                        <small><strong class="item-title">{{ item.winner_discretionary_title }}</strong></small>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div class="row">
              <div id="pages" class="col">
                <p class="text-center">
                  <a v-if="this.currentPage > 3" @click="this.previousPage()" href="javascript:void(0)"
                    class="pr-2 pl-2"><strong>Prev</strong></a>
                  <span v-for="page in this.myPages" :key="page">
                    <a :class="['pr-2 pl-2']" href="javascript:void(0)" @click="this.setPage(page)">
                      <strong v-if="this.currentPage == page">{{ page }}</strong>
                      <span v-else>{{ page }}</span>
                    </a>
                  </span>
                  <a v-if="this.currentPage + 1 < this.maxPage" @click="this.nextPage(currentPage + 1)"
                    href="javascript:void(0)" class="pr-2 pl-2"><strong>Next</strong></a>

                </p>
              </div>
            </div>

          </div>
        </div>
      </div>
    </div>
  </div>



  <!-- Optional JavaScript -->
  <!-- jQuery first, then Popper.js, then Bootstrap JS -->


</template>

<script>
import $ from 'jquery'
import jsonData from "../assets/json/json.json"
import App from "../assets/js/app.js"
export default {
  name: 'HelloWorld',
  props: {
    msg: String
  },
  methods: {
    filter: App.filter,
    /**FILTER BY PARAMETERS */
    filterByParameters() {


      var quarterArray = [];
      var categArray = [];
      var countryArray = [];
      var searchArray = [];
      var running_q = [];
      var award_cat_title = [];
      var winner_region = [];
      var filterByParameters = [];



      if (this.selQuarter != '') {
        quarterArray.push(this.selQuarter);
        running_q = quarterArray;
        filterByParameters['running_q'] = running_q;
      }

      if (this.selCategory != '') {
        categArray.push(this.selCategory);
        award_cat_title = categArray;
        filterByParameters['award_cat_title'] = award_cat_title;
      }

      if (this.selCountry != '') {
        countryArray.push(this.selCountry);
        winner_region = countryArray;
        filterByParameters['winner_region'] = winner_region;
      }

      if (this.txtSearch != '') {
        searchArray.push(this.txtSearch);
      }


      var data = JSON.stringify(jsonData.item);
      data = JSON.parse(data);
      // filterByParameters = { award_cat_title: ["Business Growth"], BALLOT_STATUS: ["PERM", "POLL"] }
      // var filterByParameters = { award_cat_title: categArray, winner_region: regionArray };
      var result = data.filter(function (o) {
        return Object.keys(filterByParameters).every(function (k) {
          return filterByParameters[k].some(function (f) {
            return o[k] === f;
          });
        });
      });


      /*** 
       * Text Filter
       * 
       * 
       * **/
      if (this.txtSearch != "") {
        result = JSON.stringify(result);
        result = JSON.parse(result);
        var search = this.txtSearch;


        if (!search.includes("@")) {
          console.log("Searching full name...");
          result = JSON.stringify(result);
          result = JSON.parse(result);
          result = result.filter(i => i.winner_full_name.toLowerCase().includes(search.toLowerCase()));
        } else {
          console.log("Searching email address...");
          result = JSON.stringify(result);
          result = JSON.parse(result);
          result = result.filter(i => i.winner_email.toLowerCase().includes(search.toLowerCase()));
        }
      }

      var arrPages = [];
      var minCounter = 1;
      var i = 1;

      //page counter
      var maxCounter = (this.currentPage * 12);
      minCounter = ((maxCounter + 1) - 12);
      var maxPage = Math.ceil(result.length / 12);
      //getting min max counter
      this.minCounter = minCounter;
      this.maxCounter = maxCounter;
      this.maxPage = maxPage;

      for (i = 1; i <= maxPage; i++) {
        arrPages.push(i);
      }

      this.myPages = arrPages;
      var counter = 1;
      var newItems = [];
      $(result).each(function () {
        if (counter >= minCounter && counter <= maxCounter) {
          newItems.push(this);
        }
        counter++;
      });
      console.log(this);
      this.items = newItems;

      return this.items;
    },
    //paginator
    setPage(page) {
      this.currentPage = page;
      this.filterByParameters();
    }, previousPage() {
      this.currentPage = this.currentPage - 1;
      this.filterByParameters();
    }, nextPage() {
      this.currentPage = this.currentPage + 1;
      this.filterByParameters();
    }
  },
  //all data on components
  data() {
    return {
      currentPage: 1,
      minCounter: 1,
      maxCounter: 1,
      maxPage: 1,
      myPages: [],
      data: jsonData,
      items: App.formattedJson(),
      categ: [],
      region: [],
      quarter: [],
      selCategory: "",
      selCountry: "",
      selQuarter: "",
      txtSearch: "",
      categories: App.getCategories(jsonData.item),
      countries: App.getCountries(jsonData.item),
      quarters: App.getQuarters(jsonData.item),
      pageCount: 0
    }
  },
  computed: {
    computedItems() {
      return this.filterByParameters
    }
  }, created() {
    /***
     * ON Document load, run page visit counter
     */
    var myStorage = window.localStorage, pageCount;
    window.addEventListener('load', function () {
      if (!myStorage.getItem("pageCount")) {
        myStorage.setItem('pageCount', 1);
      } else {
        pageCount = myStorage.getItem("pageCount") * 1;
        pageCount = pageCount + 1;
        myStorage.setItem('pageCount', pageCount);
      }
      console.log('page view count', myStorage.getItem("pageCount"));
    });
    this.pageCount = myStorage.pageCount;
    /**
     * ON Document load, run filter by parameters
     * **/
    this.filterByParameters();
  }
}
</script>


